import { Form, message } from "antd";
import RouteStore from "../store/RouteStore";
import { LoginReq } from "./Course";
import Input from "antd/lib/input/Input";
import Button from "antd/es/button";
import { observer } from "mobx-react-lite";
import { useNavigate } from "react-router-dom";
import { useEffect } from "react";

function Login() {
  function onFinish(value: LoginReq) {
    console.log(value)
    RouteStore.login(value);
  }

  const nav = useNavigate();
  useEffect(() => {
    if (RouteStore.state === "done") {
      nav("/");
    } else if (RouteStore.state === "error") {
      message.error(RouteStore.message);
    }
  }, [RouteStore.state]);

  return (
    <div style={{ paddingTop: 200 }}>
      <Form
        name="basic"
        labelCol={{ span: 8 }}
        wrapperCol={{ span: 8 }}
        onFinish={onFinish}
      >
        <Form.Item
          label="用户名"
          name={"username"}
          rules={[{ required: true, message: "请输入用户名" }]}
        >
          <Input autoComplete="off"></Input>
        </Form.Item>

        <Form.Item
          label="密码"
          name={"password"}
          rules={[{ required: true, message: "请输入密码" }]}
        >
          <Input type="password" autoComplete="off"></Input>
        </Form.Item>

        <Form.Item wrapperCol={{ offset: 8, span: 6 }}>
          <Button type="primary" htmlType="submit">
            登录
          </Button>
        </Form.Item>
      </Form>
    </div>
  );
}

export default observer(Login);
